
export default {
  props: {
    title: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: true
    },
    routeName: {
      type: String,
      required: true
    }
  },
  computed: {
    to () {
      let industry = this.$route.params.industry
      if (!industry) {
        industry = this.$store.state.company.上市上櫃產業編碼
      }
      return {
        name: this.routeName,
        params: {
          industry
        }
      }
    },
    isActive () {
      return this.$route.name === this.routeName
    },
    iconPath () {
      const postfix = this.isActive ? '-active' : '-default'
      return `/logo/${this.icon}${postfix}.svg`
    }
  }
}
